@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,700;1,400&display=swap);
.active {
  color: white !important;
}

div.nav-dropdown {
  top: 48px;
}

.button {
  background-color: rgba(55, 190, 58, 0.9);
}

.social-link-day {
  background-color: rgb(185, 81, 6);
  background: radial-gradient(transparent 60%, black) #e76508;
  background-color: rgb(185, 81, 6);
  background-image: radial-gradient(rgba(0, 0, 0, 0) 60%, rgb(0, 0, 0));
}

html {
  margin: 0;
  background-color: var(--background);
}

body {
  overflow-x: hidden;
  margin: 0;
}

